import React, { Component } from 'react'
import '../../Styles/ChatBot.css'
import vector from '../../images/Vector1.png'
import rep from '../../images/Ellipse 37.png'
import name from '../../images/Name.png'
import repot from '../../images/rebot 1.png'

export default function ChatBot() {
   
    return (
      <div className='chatbot'>
        <div className='container'>
        <div class="card mb-3">
          <img src={vector} class="card-img-top" alt="..." />
          <div class="card-img-overlay ms-5">
            <div className='rep'>
            <img src={rep} alt="" />
            <h2>INFANTCARE</h2> 
            </div>
            <p className='p_rep'>made with love</p>
          </div>
          <div class="card-body">
            <div class="row">
              <div class="col-sm-8">
                <form action="">
                <div class="mb-3">
                  <ul className='det_message'>
                    <li><img src={name} alt="" /></li>
                    <li><input type="text" class="form-control" placeholder='ask your question....' id="exampleInputEmail1"  /></li>
                    <a href="#"><li className='icon'><i class="bi bi-send-fill"></i></li></a>
                  </ul>
                </div>
                </form>
              </div>
              <div class="col-sm-4">
                <div className='report'>
                <div class="card border-0">
                  <div class="card-header">
                    Hi,User
                  </div>
                  <img src={repot} alt="" />
                </div>
                </div>
              </div>
          </div>
          </div>
        </div>
        </div>
      </div>
    )
  }
