import axios from "axios";
import { Navigate } from "react-router-dom";

const apiClient = axios.create({
  baseURL: "http://localhost:3000/api/v1/",
});

export const sendFriendInvitation = async (data) => {
  try {
    return await apiClient.post("users/addFriend", data, {
      headers: {
        token: localStorage.getItem("token"),
      },
    });
  } catch (exception) {
    checkResponseCode();
    return {
      error: true,
      exception,
    };
  }
};

export const acceptFriendInvitation = async (data) => {
  try {
    return await apiClient.post("users/acceptAddFriend", data, {
      headers: {
        token: localStorage.getItem("token"),
      },
    });
  } catch (exception) {
    checkResponseCode();
    return {
      error: true,
      exception,
    };
  }
};

export const rejectFriendInvitation = async (data) => {
  try {
    return await apiClient.post("users/rejectAddFriend", data, {
      headers: {
        token: localStorage.getItem("token"),
      },
    });
  } catch (exception) {
    checkResponseCode();
    return {
      error: true,
      exception,
    };
  }
};

const checkResponseCode = (exception) => {
  const responseCode = exception?.response?.status;
  if (responseCode) {
    (responseCode === 401 || responseCode === 403) && localStorage.clear() && (
      <Navigate to="/auth/signin" />
    );
  }
};
