import React, { useEffect, useState } from "react";
import Logo from "../../images/LOGO 1.svg";
import { Link, NavLink } from "react-router-dom";
import "./Navbar.css";
import DropdownMenu from "../../Dashboard/AppBar/DropdownMenu.js";
import { jwtDecode } from "jwt-decode";
import axios from "axios";
import { baseURL } from "../../utils/baseURL.js";

// Throttle function to limit the frequency of scroll event handling
const throttle = (callback, delay) => {
  let lastTime = 0;
  return function () {
    const now = new Date().getTime();
    if (now - lastTime >= delay) {
      callback();
      lastTime = now;
    }
  };
};

const getUserData = async (setUser) => {
  try {
    const token = localStorage.getItem("token");
    const decoded = jwtDecode(token);
    const { data } = await axios.get(`${baseURL}users/${decoded.userId}`, {
      headers: { token: localStorage.getItem("token") },
    });
    setUser(data.result);
    localStorage.setItem("profileImage", data.result.profileImage?.url);
  } catch (error) {
    console.error("Error fetching user data:", error);
  }
};

export default function Navbar() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [user, setUser] = useState("");

  useEffect(() => {
    getUserData(setUser);

    const handleScroll = throttle(() => {
      setIsScrolled(window.scrollY > 100);
    }, 50);

    window.addEventListener("scroll", handleScroll);
  }, []);

  return (
    <>
      <nav
        className={`navbar navbar-expand-lg ${isScrolled ? "scrolled" : ""}`}
      >
        <div className="container-fluid px-5">
          <Link className="navbar-brand" to="/">
            <img src={Logo} alt="" />
          </Link>
          <span
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <i className="fa-solid fa-bars"></i>
          </span>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink className="nav-link" to="/home">
                  Home
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/aboutus">
                  About
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/chatbot">
                  Chatbot
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/">
                  Supplies
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/contactus">
                  Contact
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/">
                  Book a...
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/">
                  Read or Watch...
                </NavLink>
              </li>
            </ul>

            <DropdownMenu
              className="navbar-nav ms-auto mb-2 mb-lg-0"
              user={user}
            />
          </div>
        </div>
      </nav>
    </>
  );
}
