import React, { useEffect } from "react";
import { RouterProvider, createBrowserRouter } from "react-router-dom";
import "./App.css";
import Dashboard from "./Dashboard/Dashboard.js";
import Home from "./Components/Home";
import Login from "./Components/LoginAndSiginup/Login";
import Register from "./Components/LoginAndSiginup/Signup";
import Article from "./Components/Articles/Articles";
import Doctors from "./Components/Doctors/Doctors";
import Celendar1 from "./Components/Doctors/celendar/Celendar";
import AboutUs from "./Components/AboutUs/AboutUs";
import DetailsDoctors from "./Components/Doctors/DetailsDoctor";
import ArticleDetails from "./Components/Articles/ArticleDetails";
import ContactUs from "./Components/ContactUs/ContactUs";
import ChatBot from "./Components/Chatbot/ChatBot";
import { ToastContainer } from "react-toastify";
import ProductDesply from "./Components/product/ProductDesply";
import { Offline } from "react-detect-offline";
import MainLayout from "./Components/Layouts/MainLayout.jsx";
import ProtectedRoutes from "./Components/ProtectedRoutes/ProtectedRoutes.jsx";
import Notfound from "./Components/Notfound/Notfound.jsx";
import AuthLayout from "./Components/Layouts/AuthLayout.jsx";
import UpdateProfile from "./Components/UpdateProfile/UpdateProfile.jsx";
import { connectWithSocketServer } from "./realtimeCommunication/socketConnection.js";

function App() {
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (token) {
      connectWithSocketServer(token);
    }
  }, []);

  const routes = createBrowserRouter([
    {
      path: "/",
      element: <MainLayout />,
      children: [
        { index: true, element: <Home /> },
        { path: "signup", element: <Register /> },
        { path: "login", element: <Login /> },
        { path: "profile", element: <UpdateProfile /> },
        { path: "home", element: <Home /> },
        { path: "art", element: <Article /> },
        { path: "doctors", element: <Doctors /> },
        { path: "celendar1", element: <Celendar1 /> },
        { path: "aboutus", element: <AboutUs /> },
        { path: "detailsdoctors", element: <DetailsDoctors /> },
        { path: "artdet", element: <ArticleDetails /> },
        { path: "contactus", element: <ContactUs /> },
        { path: "dashboard", element: <Dashboard /> },
        {
          path: "chatbot",
          element: (
            <ProtectedRoutes>
              <ChatBot />
            </ProtectedRoutes>
          ),
        },
        {
          path: "productDesply",
          element: (
            <ProtectedRoutes>
              <ProductDesply />
            </ProtectedRoutes>
          ),
        },
        { path: "*", element: <Notfound /> },
      ],
    },
    {
      path: "/auth",
      element: <AuthLayout />,
      children: [
        { path: "signup", element: <Register /> },
        { path: "login", element: <Login /> },
      ],
    },
  ]);

  return (
    <div className="App">
      <RouterProvider router={routes} />
      <Offline>
        <div className="offline">You are Offline Now!</div>
      </Offline>
      <ToastContainer theme="colored" autoClose={3000} />
    </div>
  );
}

export default App;
